export const cityList = addressList.map((address) => {
  const city = address[0]
  return {
    label: city,
    value: city,
  }
})

export const renderAreaList = (city) => {
  if (city.length === 0) return []
  const findArea = address => address[0] === city
  return addressList
    .find(findArea)
    .slice(1)
    .map(addr => ({ value: addr, label: addr }))
}
